import React, { useRef } from "react";
import styles from "./faculty-list.module.css";

export default FacultyList = () => {
  const sliderRef = useRef(null);

  // Dynamic data structure
  const facultyData = {
    subtitle: "FACULTY LIST",
    title: {
      highlighted: "GUIDING MINDS",
      normal: " OF COMPUTER SCIENCE & ENGINEERING",
    },
    members: [
      {
        id: 1,
        name: "Dr. Anjali Mehra",
        designation: "Assistant Professor",
        image: "/path-to-image1.jpg",
      },
      {
        id: 2,
        name: "Dr. Jaspreet Kaur",
        designation: "Assistant Professor",
        image: "/path-to-image2.jpg",
      },
      {
        id: 3,
        name: "Vinooth P",
        designation: "Assistant Professor",
        image: "/path-to-image3.jpg",
      },
      {
        id: 4,
        name: "Dr. Anjali Mehra",
        designation: "Assistant Professor",
        image: "/path-to-image4.jpg",
      },
      {
        id: 5,
        name: "Dr. Rajesh Kumar",
        designation: "Associate Professor",
        image: "/path-to-image5.jpg",
      },
      {
        id: 6,
        name: "Dr. Priya Singh",
        designation: "Assistant Professor",
        image: "/path-to-image6.jpg",
      },
    ],
  };

  const scrollLeft = () => {
    if (sliderRef.current) {
      sliderRef.current.scrollBy({ left: -350, behavior: "smooth" });
    }
  };

  const scrollRight = () => {
    if (sliderRef.current) {
      sliderRef.current.scrollBy({ left: 350, behavior: "smooth" });
    }
  };

  return (
    <div className={styles.container}>
      <div className="container">
        {/* Header Section */}
        <div className={styles.headerSection}>
          <p className={styles.subtitle}>{facultyData.subtitle}</p>
          <h2 className={styles.title}>
            <span className={styles.titleHighlighted}>
              {facultyData.title.highlighted}
            </span>
            <span className={styles.titleNormal}>
              {facultyData.title.normal}
            </span>
          </h2>
        </div>

        {/* Slider Container */}
        <div className={styles.sliderWrapper}>
          {/* Left Arrow */}
          <button className={styles.navButton} onClick={scrollLeft}>
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
            >
              <path d="M15 18l-6-6 6-6" />
            </svg>
          </button>

          {/* Faculty Cards Slider */}
          <div className={styles.slider} ref={sliderRef}>
            {facultyData.members.map((member) => (
              <div key={member.id} className={styles.facultyCard}>
                <div className={styles.imageContainer}>
                  <div className={styles.imagePlaceholder}>
                    <svg
                      width="60"
                      height="60"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="1"
                    >
                      <rect x="3" y="3" width="18" height="18" rx="2" ry="2" />
                      <circle cx="8.5" cy="8.5" r="1.5" />
                      <polyline points="21 15 16 10 5 21" />
                    </svg>
                    <p className={styles.imageText}>IMAGE</p>
                  </div>
                  {/* Uncomment when you have actual images */}
                  {/* <img src={member.image} alt={member.name} className={styles.facultyImage} /> */}
                </div>
                <div className={styles.facultyInfo}>
                  <h3 className={styles.facultyName}>{member.name}</h3>
                  <p className={styles.facultyDesignation}>
                    {member.designation}
                  </p>
                  <div className={styles.underline}></div>
                </div>
              </div>
            ))}
          </div>

          {/* Right Arrow */}
          <button className={styles.navButton} onClick={scrollRight}>
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
            >
              <path d="M9 18l6-6-6-6" />
            </svg>
          </button>
        </div>
      </div>

      {/* Bootstrap CSS CDN */}
      <link
        href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css"
        rel="stylesheet"
      />
    </div>
  );
};
